
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IVoucherForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import VoucherForm from '@/views/sale/voucher/forms/VoucherForm.vue'

export interface IData {
  model: IVoucherForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [saveForm, saveStatus, saveError] = useFetching((dataForm: IVoucherForm) => {
      return store.dispatch('voucher/createForm', dataForm)
    }) as ResponseFetching<Promise<IVoucherForm>>

    const form = ref()

    return {
      saveForm,
      saveStatus,
      saveError,
      form
    }
  },

  components: {
    PageCard,
    VoucherForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: {
        code: '',
        fromName: '',
        fromEmail: '',
        toName: '',
        toEmail: '',
        voucherThemeId: 0,
        message: '',
        amount: '',
        isActive: true
      }
    }
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'voucher' })
    },

    async handleSubmit () {
      if (this.form && await this.form.validate()) {
        const data: IVoucherForm = await this.saveForm(this.model)
        if (typeof data !== 'undefined') {
          this.model = data
        }

        if (this.model?.id) {
          this.$router.push({ name: 'voucher-update', params: { id: this.model.id } })
        }
      }
    }
  }
})
